<template>
  <div class="homepage">
    <div class="home_img">
      <el-image :src=home fit="fill" class="home_image" :preview-src-list=[home]></el-image>
    </div>
    <div class="longer-padding">
    <ContentCard
      :title=module_1_title
      :description=module_1_text
      :link="{ text: 'View backgrounds', url: '#' }"
      :image=module1
    />
    <div class="placeholder"></div>
    <div class="news-list">
      <el-header class="title">
        News
      </el-header>
    <NewsItem
      class="news-home"
      v-for="(item, index) in news"
      :key="index"
      :title="item.title"
      :description="item.description"
      :image="item.image"
      :time="item.time"
      :maxLength="140"
    />
    <div class="card-link">
      <RouterLink  to='/news' class="no-underline">More</RouterLink>
    </div>
  </div>
  <div class="timeline"><vue-timeline></vue-timeline></div>
</div>
  </div>
</template>

<script>
import MarkdownIt from 'markdown-it'
import MavonEditor from 'mavon-editor'
import home from '@/assets/home.jpg'
import module1 from '@/assets/home_module1.png'
import ContentCard from '@/components/ContentCard.vue'
import NewsItem from './NewsItem.vue'
import TimeLine from './TimeLine.vue'
import { RouterLink } from 'vue-router'
import VueTimeline from '@/components/VueTimeline.vue'
export default {
  name: 'HomePage',
  components: {
    MavonEditor,
    ContentCard,
    NewsItem,
    RouterLink,
    TimeLine,
    VueTimeline
  },
  data () {
    return {
      home: home,
      module1: module1,
      module_1_title: 'LOONG: LLM-powered autOnomous Optical Network aGent',
      module_1_text: `LOONG is a field trial project aimed at developing an intelligent agent for L4 and L5 level autonomous driving optical networks. Our objective is to create near-realistic demonstrations that bridge the gap between academia and industry, facilitating the deployment of various advanced network management applications that utilize AI techniques. We aim to design and implement a diverse range of advanced algorithms on the testbed to showcase the potential of using AI for Autonomous Driving Optical Networks`,
      news: [
        {
          title: 'Two papers based on LOONG testbed are accepted by OFC 2025!',
          time: '[12/2024]'
        },
        {
          title: 'The website of LOONG project is established!',
          time: '[12/2024]'
        },
        {
          title: 'We are happy to share our latest work named “First Field Trial of LLM-Powered AI Agent for Lifecycle Management of Autonomous Driving Optical Networks”based on LOONG testbed !',
          time: '[12/2024]'
        },
        {
          title: 'Prof. Zhuge is invited to give a talk in OFC 2025!',
          time: '[12/2024]'
        }
      ],
      module3_markdown: '# 研究方向和研究脉络\n - LLM-powered agent for Network automation\n 简要概括\n - Digital twins for optical networks\n 简要概括',
      toolbars: {
        bold: true, // 加粗按钮
        italic: true, // 斜体按钮
        header: true // 标题按钮
        // 可以根据需要添加更多工具按钮，参考文档：https://github.com/hinesboy/mavonEditor
      }
    }
  },
  created () {
    console.log('HomePage一创建')
  },
  computed: {
    renderedHtml2 () {
      const md = new MarkdownIt()
      return md.render(this.module2_markdown)
    },
    renderedHtml3 () {
      const md = new MarkdownIt()
      return md.render(this.module3_markdown)
    }
  },
  methods: {
    handleChange (value, render) {
      console.log('Markdown 内容发生改变:', value)
      console.log('渲染后的 HTML:', render)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home_img{
  margin: 2em 0 0 0;
}
.module_1_text{
  text-align:left
}
.module1_image:hover{
  transform: scale(1.05);
}

.markdown-preview {
  text-align: left !important;
}
  .el-aside {
    color: #333;
    text-align: center;
    padding: 2em 0 1em 10em;
    line-height: 1.8;
    font-size: 18px;
    font-family: "微软雅黑"
  }
  .el-main {
    color: #333;
    text-align: left;
    width:100%;
  }
  .timeline{
    padding: 0;
  }
  .news-list {
  max-width: 100%;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}
.title{
  font-size: 30px;
  line-height: 80px;
  font-weight: bold;
  text-align: center;
  height: 100px
}
.card-link {
font-size: 1.3rem;
color: #0078d4;
font-weight: bold;
text-decoration: none;
padding: 20px 30px;
line-height: 10px;
display: flex;
justify-content: start;
}

.card-link:hover {
text-decoration: underline;
}
.no-underline{
  text-decoration-line:none;
}
.longer-padding{
  padding:0 150px
}
.timeline{
  display: flex;
  flex: 1;
  padding-top: 50px;
}
.home_image{
  height: 500px
}
.placeholder{
  height: 50px
}
</style>



