<template>
    <div>
      <!-- 图片列表 -->
      <div class="image-container">
        <img
          v-for="(image, index) in images"
          :key="index"
          :src="image"
          :alt="'Image ' + index"
          class="image"
          @click="openFullscreen(image)"
        />
      </div>
      <!-- 全屏显示图片 -->
      <div v-if="isFullscreen" class="fullscreen" @click="closeFullscreen">
        <img :src="currentImage" alt="Fullscreen Image" />
      </div>
    </div>
  </template>
<script>
import Publications11 from '@/assets/publications_1_1.png'
export default {
  data () {
    return {
      Publications11: Publications11,
      images: [
        Publications11,
        Publications11
      ], // 图片列表
      isFullscreen: false, // 是否处于全屏状态
      currentImage: '' // 当前全屏显示的图片
    }
  },
  methods: {
    openFullscreen (image) {
      this.currentImage = image
      this.isFullscreen = true
    },
    closeFullscreen () {
      this.isFullscreen = false
      this.currentImage = ''
    },
    handleKeydown (event) {
      if (event.key === 'Escape' && this.isFullscreen) {
        this.closeFullscreen()
      }
    }
  },
  mounted () {
    document.addEventListener('keydown', this.handleKeydown)
  },
  beforeDestroy () {
    document.removeEventListener('keydown', this.handleKeydown)
  }
}
</script>
  <style scoped>
  /* 图片容器样式 */
  .image-container {
    display: flex;
    gap: 10px;
  }
  .image {
    max-width: 100px;
    height: auto;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  .image:hover {
    transform: scale(1.1);
  }
  /* 全屏样式 */
  .fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  .fullscreen img {
    max-width: 90%;
    max-height: 90%;
    display: block;
    cursor: zoom-out;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  }
</style>



