<template>
    <div class="card">
      <div class="card-content">
        <h2 class="card-title">{{ title }}</h2>
        <p class="card-description">{{ description }}</p>
        <!-- <a :href="link.url" class="card-link">{{ link.text }} →</a> -->
      </div>
      <div class="card-image">
        <el-image :src=image :alt="title" fit="fill" :preview-src-list=[image] />
      </div>
    </div>
</template>
<script>
export default {
  name: 'ContentCard',
  props: {
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    link: {
      type: Object,
      required: true,
      default: () => ({ text: '', url: '#' })
    },
    image: {
      type: String,
      required: true
    }
  }
}
</script>

<style scoped>
.card {
display: flex;
flex-wrap: wrap;
gap: 20px;
padding: 50px;
background-color: #fff;
border-radius: 10px;
box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
align-items: center;
}

.card-content {
flex: 1;
min-width: 300px;
}

.card-title {
font-size: 1.8rem;
font-weight: bold;
margin-bottom: 10px;
text-align: left;
}

.card-description {
font-size: 1rem;
margin-bottom: 20px;
color: #555;
text-align: left;
}

.card-link {
font-size: 1rem;
color: #0078d4;
font-weight: bold;
text-decoration: none;
}

.card-link:hover {
text-decoration: underline;
}

.card-image {
flex: 1;
text-align: center;
}

.card-image img {
max-width: 100%;
border-radius: 10px;
}
</style>



