<template>
  <div class="news-list longer-padding">
    <NewsItem
      v-for="(item, index) in news"
      :key="index"
      :category="item.category"
      :date="item.date"
      :title="item.title"
      :description="item.description"
      :image="item.image"
      :time="item.time"
    />
  </div>
</template>

<script>
import NewsItem from './NewsItem.vue'
import qiuqizhi from '@/assets/qiuqizhi.png'
export default {
  name: 'NewsList',
  components: {
    NewsItem
  },
  data () {
    return {
      qiuqizhi: qiuqizhi,
      news: [
        {
          title: 'Two papers based on LOONG testbed are accepted by OFC 2025!',
          time: '[12/2024]'
        },
        {
          title: 'The website of LOONG project is established!',
          time: '[12/2024]'
        },
        {
          title: 'We are happy to share our latest work named “First Field Trial of LLM-Powered AI Agent for Lifecycle Management of Autonomous Driving Optical Networks”based on LOONG testbed !',
          time: '[12/2024]'
        },
        {
          title: 'Prof. Zhuge is invited to give a talk in OFC 2025!',
          time: '[12/2024]'
        }
      ]
    }
  }
}
</script>

<style scoped>
.news-list {
  max-width: 100%;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}
.longer-padding{
  padding:0 150px
}
</style>



