<template>
    <div class="news-item" @click="click">
      <div class="news-main">
        <div>
          <h3>{{ time }}</h3>
        </div>
        <div class="news-text">
          <h3 class="news-title">{{ title }}</h3>
          <div :class="{ 'text-ellipsis': maxLength }"
          :style="{ maxHeight: maxLength ? maxLength + 'px' : 'none' }">
            <p class="news-description">{{ description }}</p>
          </div>
        </div>
        <div class="news-image" v-if="image">
          <el-image :src="image" :alt="title" :preview-src-list=[image] />
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'NewsItem',
  props: {
    maxLength: {
      type: [Number, null], // 限制最大宽度（像素），为 null 时不限制
      default: null
    },
    title: {
      type: String,
      required: true
    },
    time: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    image: {
      type: String,
      required: false
    }
  },
  methods: {
    click () {
      console.log(this.maxLength)
    }
  }
}
</script>

<style scoped>
.news-item {
display: flex;
flex-direction: column;

padding: 30px;
border-bottom: 1px solid #ddd;
overflow: hidden; /* 隐藏超出内容 */
text-overflow: ellipsis; /* 超出部分显示省略号 */
}

.news-meta {
font-size: 0.9rem;
color: #666;
margin-bottom: 10px;
display: flex;
gap: 10px;
}

.news-main {
display: flex;
justify-content: space-between;

gap: 20px;
}

.news-text {
flex: 1;
text-align: left;
}

.news-title {
font-size: 1.2rem;
font-weight: bold;
}

.news-description {
font-size: 1rem;
margin-bottom: 10px;
color: #333;
}

.news-author {
font-size: 0.9rem;
color: #888;
}

.news-image {
width: 300px;
flex-shrink: 0;
}

.news-image img {
width: 100%;
height: auto;
border-radius: 5px;
}
.text-ellipsis {
  display: -webkit-box; /* 必须设置，用于启用多行截断 */
  -webkit-box-orient: vertical; /* 必须设置，用于指定文本方向 */
  -webkit-line-clamp: 5; /* 限制显示的行数，比如这里限制为 3 行 */
  overflow: hidden; /* 隐藏超出的部分 */
}
</style>



