<template>
  <div class="homepage">
  <div class="navi_bar">
    <div
      v-for="(item,index) in nav_list" :key="index" class="nav-item" >
        <router-link :to="item.path">{{ item.text }}</router-link>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      nav_list: [
        {text: '首页', path: '/'},
        {text: '平台介绍', path: '/demo'},
        {text: '数据集和开源代码', path: '/dataset'},
        {text: '研究课题和论文清单', path: '/paper'},
        {text: '新闻', path: '/news'},
        {text: '活动', path: '/activity'},
        {text: '团队', path: '/group'},
        {text: '合作单位', path: '/coauthor'}]
    }
  },
  created () {
    console.log('HomePage一创建')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav-bar {
    display: flex;
    align-items: center;
    background-color: #f8f9fa;
    padding: 0 0 0 0;
    margin: 0 0 0 0;
}
.nav-item {
  background-color: #FFCCCC;
  color: #FFFFFF;
  height: 50px;
  width: 12.3%;
  cursor: pointer;
  position: relative;
  display: inline-block;
  margin: 0;
  text-align: center;
  line-height: 50px;
}
.nav-item:last-child {
  border-right: none;
}
.nav-item:hover {
  background-color: #e0e0e0;
}
</style>



