<template>
  <div class="demopage no_border longer-padding">
    <el-container>
      <el-header class="title no_border">
        Testbed
      </el-header>
      <el-main>
        <el-image :src=testbed alt="Testbed Image" class="testbed-image" :preview-src-list=[testbed] ></el-image>
      </el-main>
      <el-aside width="100%" class="testbed_text">The field-deployed testbed with commercial equipment starts from Shanghai via Jiaxing to Hangzhou in China. It consists of a 440-km loop link with four spans of G.652.D fibers and six OAs. The testbed supports millisecond-level telemetry data collection. These telemetry data are transmitted through the optical supervisory channel (OSC) to the SDN server.</el-aside>
    </el-container>
      <el-header class="title">
        Demo
      </el-header>
    <div class="media-list">
    <DemoItem
      v-for="(item, index) in tasks"
      :key="index"
      :image="item.image"
      :title="item.title"
      :description="item.description"
      :video="item.video"
    />
  </div>
  </div>
</template>

<script>
import task1Png from '@/assets/task1.png'
import task1Mp4 from '@/assets/task1.mp4'
import task2Png from '@/assets/task2.png'
import task2Mp4 from '@/assets/task2.mp4'
import testbed from '@/assets/testbed.png'
// import task3Png from '@/assets/task3.png'
import DemoItem from '@/components/DemoItem.vue'
export default {
  name: 'demo',
  components: {
    DemoItem
  },
  data () {
    return {
      testbed: testbed,
      tasks: [
        {
          title: 'Task 1: Failure management',
          description: `In the fiber aging management, the Agent’s integrated time series forecasting module triggers the handling process when predicting a 5-dB optical power degradation. The Planner then autonomously generates a workflow using the LLM. Following this workflow, the Agent retrieves relevant documentation and queries real-time optical power measurements to calculate the attenuation value of each span, which is then compared with the standard value. Based on the comparison and analysis, the Agent can localize the fiber with anomalous attenuation. After that, the Agent can reconfigure the optical amplifiers to mitigate the transmission performance degradation. The implementation results demonstrate LLM’s strong reasoning capability as it can process diverse failure-related information across multiple sources and formats.`,
          image: task1Png,
          video: task1Mp4
        },
        {
          title: 'Task 2: Autonomous power optimization',
          description: `This demo explore the possibilities offered by LLMs to conduct power optimization in the LLM-native mode. The system utilizes LLM as a direct optimizer through the M.C. module, generating configuration suggestions via Planner and deploying them to the experimental testbed through the M.A. module. Using six amplifier gains as optimization parameters, the system evaluates optimization effectiveness through the average Q-factor of six receivers. Based on the ReAct framework, LLMs perform optimization through observe-think-act loops. The record shows how the LLM formulates a strategic plan (dividing the optimization into exploration and refinement phases), executes actions ('set_all_EDFAgain'), observes the results (Q_average), and demonstrates reasoning in its thought process for subsequent steps. Results show that the average Q-factor is successfully improved by LLM’s optimization.`,
          image: task2Png,
          video: task2Mp4
        }
      ]
    }
  },
  created () {
    console.log('HomePage一创建')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
  font-size: 30px;
  line-height: 80px;
  font-weight: bold;
  text-align: center;
  height: 100px
}
.no_border{
  border: none;
  box-shadow: none;
}
.longer-padding{
  padding:0 150px
}
.testbed_text{
  text-align: left;
  padding: 20px;
}
</style>



