<template>
  <div class="paperpage longer-padding ">
    <el-header class="title">
      1. Agent and autonomous driving optical networks
      </el-header>
      <div class="paper-box" v-for="(item, index) in publications_1" :key="index">
      <div class="paper-box-image">
        <div>
          <div class="badge">{{item.publication_group}}</div>
          <el-image :src=item.image alt="sym" width="100%" :preview-src-list=[item.image] />
        </div>
      </div>
      <div class="paper-box-text">
        <p>
          <a :href="item.link">{{ item.title }}</a> <br>
        <strong> {{ item.first_authors }}</strong>{{ item.other_authors }}
      </p>
    <ul v-for="(sub_item, index) in item.discription" :key="index">
      <li>Accepted by <a :href=item.publication_link>{{item.accept}}</a></li>
      <li> <template v-if="sub_item.link !== ''">
    <a :href="sub_item.link" target="_blank" rel="noopener noreferrer">{{ sub_item.content }}</a>
  </template>
  <template v-else>
    {{ sub_item.content }}
  </template></li>
    </ul>
  </div>
  </div>
  <el-header class="title">
      2. Digital twins and network monitoring
      </el-header>
      <div class="paper-box" v-for="(item, index) in publications_2" :key="index">
      <div class="paper-box-image">
        <div>
          <div class="badge">{{item.publication_group}}</div>
          <el-image :src=item.image alt="sym" width="100%" :preview-src-list=[item.image] />
        </div>
      </div>
      <div class="paper-box-text">
        <p>
          <a :href="item.link">{{ item.title }}</a> <br>
        <strong> {{ item.first_authors }}</strong>{{ item.other_authors }}
      </p>
    <ul v-for="(sub_item, index) in item.discription" :key="index">
      <li>Accepted by <a :href=item.publication_link>{{item.accept}}</a></li>
      <li> <template v-if="sub_item.link !== ''">
    <a :href="sub_item.link" target="_blank" rel="noopener noreferrer">{{ sub_item.content }}</a>
  </template>
  <template v-else>
    {{ sub_item.content }}
  </template></li>
    </ul>
  </div>
  </div>
  <Image />
  </div>
</template>

<script>
import Publications11 from '@/assets/publications_1_1.png'
import Publications12 from '@/assets/publications_1_2.png'
import Publications13 from '@/assets/publications_1_3.png'
import Publications21 from '@/assets/publications_2_1.png'
import Publications22 from '@/assets/publications_2_2.png'
import Publications23 from '@/assets/publications_2_3.png'
import Publications24 from '@/assets/publications_2_4.png'
import Image from '@/components/Image.vue'
export default {
  name: 'HomePage',
  components: {
    Image
  },
  data () {
    return {
      publications_1_1: Publications11,
      publicatinos_1_2: Publications12,
      publications_1_3: Publications13,
      publications_2_1: Publications21,
      publicatinos_2_2: Publications22,
      publications_2_3: Publications23,
      publications_2_4: Publications24,
      publications_1: [
        {
          image: Publications11,
          publication_group: 'OFC 2025',
          accept: 'Optical Fiber Communication Conference (OFC) 2025',
          publication_link: 'https://www.ofcconference.org/zh-cn/home/',
          link: 'https://arxiv.org/abs/2409.14605',
          title: 'First Field Trial of LLM-Powered AI Agent for Lifecycle Management of Autonomous Driving Optical Networks',
          first_authors: '',
          other_authors: 'Xiaomin Liu, Qizhi Qiu, Yihao Zhang, Yuming Cheng, Lilin Yi, Weisheng Hu, Qunbi Zhuge',
          discription: [
            {
              content: 'We design and demonstrate the first field trial of LLM-powered AI Agent for ADON. Three operation modes of the Agent are proposed for network lifecycle management. The Agent efficiently processes wavelength add/drop and soft/hard failures, and achieves comparable performance to human-designed algorithms for power optimization.',
              link: ''
            }
          ]
        },
        {
          image: Publications12,
          publication_group: 'OFC 2025',
          accept: ' Optical Fiber Communication Conference (OFC) 2025',
          publication_link: 'https://www.ofcconference.org/zh-cn/home/',
          link: '',
          title: 'TrustOPT: Trusted Online Optimization for Autonomous Driving Optical Networks with Field-trial Demonstration',
          first_authors: '',
          other_authors: 'Qizhi Qiu, Xiaomin Liu, Yihao Zhang, Lilin Yi, Weisheng Hu, Qunbi Zhuge',
          discription: [
            {
              content: 'We propose a trusted optical power optimization strategy for ADON. Field trial experiments demonstrate reliable optimization while maintaining a 4-dB Q-factor margin. System performance remains above the threshold even with an initial 0.15-dB Q-factor margin.',
              link: ''
            }
          ]
        },
        {
          image: Publications13,
          publication_group: 'ECOC 2024',
          accept: 'European Conference on Optical Communication (ECOC) 2024',
          publication_link: 'https://www.ecoc2024.org',
          link: 'https://arxiv.org/abs/2404.01949',
          title: 'Heuristic Optimization of Amplifier Reconfiguration Process for Autonomous Driving Optical Networks',
          first_authors: '',
          other_authors: 'Qizhi Qiu, Xiaomin Liu, Yihao Zhang, Lilin Yi, Weisheng Hu, Qunbi Zhuge',
          discription: [
            {
              content: 'We propose a heuristic-based optimization scheme for reliable optical amplifier reconfiguration process in ADON. In the experiment on a commercial testbed, the scheme prevents a 1.0-dB Q-factor degradation and outperforms 98.5% random solutions.',
              link: ''
            }
          ]
        }
      ],
      publications_2: [
        {
          image: Publications21,
          publication_group: 'JOCN 2023',
          accept: 'Journal of Optical Communications and Networking Vol. 15, Issue 8, pp. C242-C262 ',
          publication_link: 'https://doi.org/10.1364/JOCN.483600',
          link: 'https://doi.org/10.1364/JOCN.483600',
          title: 'Building a digital twin for intelligent optical networks [Invited Tutorial]',
          first_authors: '',
          other_authors: 'Qunbi Zhuge, Xiaomin Liu, Yihao Zhang, Meng Cai, Yichen Liu, Qizhi Qiu, Xueying Zhong, Jiaping Wu, Ruoxuan Gao, Lilin Yi, and Weisheng Hu',
          discription: [
            {
              content: 'In this tutorial, we introduce and discuss three key technologies, including modeling, telemetry, and self-learning, to build a DT for optical networks. The principles and progress of these technologies on major impairments that affect the quality of transmission are presented, and a discussion on the remaining challenges and future research directions is provided.',
              link: ''
            }
          ]
        },
        {
          image: Publications22,
          publication_group: 'AP 2024',
          accept: 'Advanced Photonics, Vol. 6, Issue 2, 026006 (March 2024). ',
          publication_link: 'https://doi.org/10.1117/1.AP.6.2.026006',
          link: 'https://doi.org/10.1117/1.AP.6.2.026006',
          title: 'Digital twin modeling and controlling of optical power evolution enabling autonomous-driving optical networks: a Bayesian approach',
          first_authors: '',
          other_authors: 'Xiaomin Liu, Yihao Zhang, Yuli Chen, Yichen Liu, Meng Cai, Qizhi Qiu, Mengfan Fu, Lilin Yi, Weisheng Hu, Qunbi Zhuge',
          discription: [
            {
              content: 'In this work, we propose a Bayesian inference framework (BIF) to construct the digital twin of OAs and control OPE in a data-efficient manner. Only the informative data are collected to balance the exploration and exploitation of the data space, thus enabling efficient autonomous-driving optical networks (ADONs). Simulations and experiments demonstrate that the BIF can reduce the data size for modeling erbium-doped fiber amplifiers by 80% and Raman amplifiers by 60%. ',
              link: ''
            }
          ]
        },
        {
          image: Publications23,
          publication_group: 'JLT 2024',
          accept: 'Journal of Lightwave Technology, vol. 42, no. 9, pp. 3174-3183, 1 May1, 2024',
          publication_link: 'https://ieeexplore.ieee.org/abstract/document/10416326',
          link: 'https://ieeexplore.ieee.org/abstract/document/10416326',
          title: 'SMOF: Simultaneous Modeling and Optimization Framework for Raman Amplifiers in C+L-Band Optical Networks',
          first_authors: '',
          other_authors: 'Xiaomin Liu, Yihao Zhang, Meng Cai, Yichen Liu, Lilin Yi, Weisheng Hu, Qunbi Zhuge',
          discription: [
            {
              content: 'In this paper, we propose a novel scheme called SMOF — simultaneous modeling and optimization framework, which conducts RA modeling and gain profile optimization simultaneously. By iteratively freezing and unfreezing the inner parameters of the DT, the pump configuration is adjusted to achieve a desirable gain profile. ',
              link: ''
            }
          ]
        },
        {
          image: Publications24,
          publication_group: 'JOCN 2024',
          accept: 'Journal of Optical Communications and Networking Vol. 16, Issue 10, pp. 1059-1069(2024)',
          publication_link: 'https://doi.org/10.1364/JOCN.539231',
          link: 'https://doi.org/10.1364/JOCN.539231',
          title: 'Mapping-finding input-parameter refinement paradigm for a dynamic multiband optical network digital twin: the Raman amplifier modeling case',
          first_authors: '',
          other_authors: 'Yihao Zhang, Xiaomin Liu, Qizhi Qiu, Yichen Liu, Lilin Yi, Weisheng Hu, Qunbi Zhuge',
          discription: [
            {
              content: 'In this paper, an input-parameter refinement (IR) paradigm aiming at finding the mappings from uncertain parameters to their corresponding true values is proposed. The primary advantage of the IR paradigm, as demonstrated in this study for Raman amplifiers (RAs), lies in its applicability to dynamic optical networks, where system parameters such as loading conditions and optical device configurations are subject to frequent variations. The use of the proposed paradigm to refine the model of RAs is discussed in detail. Results show the proposed IR scheme can lower the physics-based RA model’s mean prediction error from ∼0.92 dB to ∼0.20 dB and lower the maximum absolute error (MAE) from ∼3.09 dB to ∼1.12 dB.',
              link: ''
            }
          ]
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
  font-size: 30px;
  line-height: 80px;
  font-weight: bold;
  text-align: start;
  padding: 20px 0 60px 0;
}
.no_border{
  border: none;
  box-shadow: none;
}
.paper-box {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    border-bottom: 1px #efefef solid;
    padding: 0 0 2em 0;
}
@media (min-width: 48em) {
.paper-box .paper-box-image {
    justify-content: left;
        min-width: 200px;
        max-width: 40%;
        order: 1;
    }
}
.badge {
    z-index: 9999;
    padding-left: 1rem;
    padding-right: 1rem;
    position: absolute;
    margin-top: .5em;
    margin-left: -.5em;
    color: white;
    background-color: #00369f;
    font-size: .8em;
}
.paper-box .paper-box-image img {
    max-width: 400px;
    box-shadow: 3px 3px 6px #888;
    object-fit: cover;
}
@media (min-width: 48em) {
    .paper-box .paper-box-text {
        justify-content: left;
        padding-left: 2em;
        max-width: 57%;
        order: 2;
    }
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    unicode-bidi: isolate;
    text-align: left;
    line-height: 1.5;
}
.page__content p {
    margin: 0 .5em .7em;
}
.page__content a {
    text-decoration: underline;
}
ul {
    padding-inline-start: 2em;
    margin-block-start: 0.5em;
}
ul li, ol li {
    margin-bottom: 0.5em;
}
li {
    display: list-item;
    text-align: left;
    unicode-bidi: isolate;
}
.longer-padding{
  padding:0 150px
}
</style>



