<template>
  <div>
  <el-image :src=group_photo fit="fill" class="group_photo"></el-image>
  <div class="group_card">
    <h1>The Executive Team</h1>
    <div class="center">
      <div class="leader">
        <img :src=leader.image :alt="name"/>
        <div class="text_space">
          <h3><a :href=leader.scholar>{{ leader.name }}</a></h3>
          <p class="role">{{ leader.role }}</p>
        </div>
    </div>
    </div>
    <div class="team">
      <div
        v-for="(member, index) in teamMembers"
        :key="index"
        :name="member.name"
        :role="member.role"
        :description="member.description"
        :image="member.image"
        class="team-member"
      >
        <img :src=member.image :alt="name" />
        <div class="text_space">
          <h3><a :href=member.scholar>{{ member.name }}</a></h3>
          <p class="role">{{ member.role }}</p>
        </div>
    </div>
    </div>
  </div>
</div>
</template>

<script>
import groupPhoto from '@/assets/group_photo.jpg'
import liuxiaomin from '@/assets/liuxiaomin.png'
import qiuqizhi from '@/assets/qiuqizhi.png'
import zhangyihao from '@/assets/zhangyihao.png'
import zhugequnbi from '@/assets/zhugequnbi.png'
export default {
  name: 'group',
  data () {
    return {
      group_photo: groupPhoto,
      zhangyihao: zhangyihao,
      zhugequnbi: zhugequnbi,
      qiuqizhi: qiuqizhi,
      liuxiaomin: liuxiaomin,
      leader: {
        name: '诸葛群碧',
        role: 'qunbi.zhuge@sjtu.edu.cn',
        description: '光网络和光传输',
        image: zhugequnbi,
        scholar: 'https://scholar.google.cz/?hl=zh-CN'
      },
      teamMembers: [
        {
          name: '刘晓敏',
          role: 'xiaomin.liu@sjtu.edu.cn',
          description: 'liuxiaomin@sjtu.edu.cn',
          image: liuxiaomin,
          scholar: 'https://scholar.google.cz/?hl=zh-CN'
        },
        {
          name: '张一豪',
          role: 'yihao.zhang@sjtu.edu.cn',
          description: 'zhangyihao@sjtu.edu.cn',
          image: zhangyihao,
          scholar: 'https://scholar.google.cz/?hl=zh-CN'
        },
        {
          name: '邱淇智',
          role: 'qizhi.qiu@sjtu.edu.cn',
          description: 'qizhi@sjtu.edu.cn',
          image: qiuqizhi,
          scholar: 'https://scholar.google.cz/?hl=zh-CN'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.group_photo{
  padding: 2em 0 0 0;
  border-radius: 5px;
  width:100%;
  height: auto;
}
.group_card {
      font-family: Arial, sans-serif;
      background-color: #2f2f3f;
      margin: 0 70px;
      padding: 20px;
      color: white;
      border-radius: 10px;
    }
    h1 {
      font-size: 2.5em;
      font-weight: bold;
      margin-bottom: 10px;
    }

    p {
      font-size: 1.1em;
      margin-bottom: 30px;
      color: #b0b0b0;
    }

    .team {
      display: grid;
      grid-template-columns: repeat(3, minmax(300px, 1fr));
      gap: 20px;
    }

    .team-member {
      background-color: white;
      color: black;
      border-radius: 10px;
      display: flex;
      align-items: center;
      padding: 15px 15px 15px 15px;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    }
    .leader{
      width:50%;
      background-color: white;
      color: black;
      border-radius: 10px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 15px 15px 15px 15px;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    }
    .leader img {
      width: 300px;
      height: 300px;
      border-radius: 10px;
      object-fit: cover;
      margin-right: 15px;
    }
    .leader p {
      margin: 5px 0 0;
      color: #555;
    }
    .leader h3 {
      margin: 0;
      font-size: 1.5em;
      color: #2f2f3f;
    }
    .leader .role {
      color: #28a745;
      font-weight: bold;
    }
    .team-member img {
      width: 150px;
      border-radius: 10px;
      object-fit: cover;
      margin-right: 15px;
    }

    .team-member h3 {
      margin: 0;
      font-size: 1.5em;
      color: #2f2f3f;
    }

    .team-member .role {
      color: #28a745;
      font-weight: bold;
    }

    .team-member p {
      margin: 5px 0 0;
      color: #555;
    }
    .text_space{
      width:100%
    }
    .center{
      display: flex;
      justify-content: center;
      padding-bottom: 20px;
    }
    a {
      text-decoration: none;
      color: black
    }
</style>



