
<template>
  <div class="block">
  <el-timeline>
    <el-timeline-item timestamp='' hide-timestamp="true" placement="top" size="large" color="#4caf50" >
      <el-card :body-style="{padding:0}" class="el-card-item custom-card">
        <div class="card-header">
            2023/06
          </div>
          <div class="card-body">
            <p>Project starts</p>
          </div>
      </el-card>
    </el-timeline-item>
    <el-timeline-item timestamp="2018/4/3" hide-timestamp="true" placement="top" size="large" color="#4caf50">
      <el-card :body-style="{padding:0}" class="el-card-item custom-card">
        <div class="card-header">
            2023/09
          </div>
          <div class="card-body">
            <p>The establishment of field trial testbed</p>
          </div>
      </el-card>
    </el-timeline-item>
    <el-timeline-item timestamp="2018/4/2"  hide-timestamp="true" placement="top" size="large" color="#4caf50">
      <el-card :body-style="{padding:0}" class="el-card-item custom-card">
        <div class="card-header">
            2024/06
          </div>
          <div class="card-body">
            <p>The establishment of lab testbed </p>
          </div>
      </el-card>
    </el-timeline-item>
    <el-timeline-item timestamp="2018/4/2"  hide-timestamp="true" placement="top" size="large" color="#4caf50">
      <el-card :body-style="{padding:0}" class="el-card-item custom-card">
        <div class="card-header">
            2024/09
          </div>
          <div class="card-body">
            <p>First demonstration of LLM-powered agent for lifecycle management</p>
          </div>
      </el-card>
    </el-timeline-item>
    <el-timeline-item color="white"></el-timeline-item>
  </el-timeline>
</div>
</template>

<script>
export default {
  name: 'TimeLine',
  data () {
    return {
      timelineEvents: [
        {
          timestamp: 'April 1',
          title: '2023/06',
          body: 'Project starts'
        },
        {
          timestamp: 'April 1',
          title: '2023/06',
          body: 'The establishment of field trial testbed '
        },
        {
          timestamp: 'April 1',
          title: '2023/09',
          body: 'The establishment of lab testbed '
        },
        {
          timestamp: 'April 1',
          title: '2023/09',
          body: 'First demonstration of LLM-powered agent for lifecycle management'
        }
      ]
    }
  }
}
</script>

<style scoped>
.el-card-item{
  min-width: 200px;
}
.custom-card {
  background-color: #d9f1d9; /* 浅绿色背景 */
  text-align: center;
  position: relative;
  width: 300px;
  border-radius: 10px;
  overflow: hidden;
  padding: 0;
  margin-bottom: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* 标题部分 */
.card-header {
  background-color: #4caf50; /* 绿色背景 */
  color: white;
  padding:8px;
  font-size: 18px;
  font-weight: bold;
  position: relative;
}
.card-body {
  padding: 15px;
  color: #4caf50;
  font-size: 23px;
  line-height: 1.5
}
</style>



