<template>
    <div class="media-item">
      <!-- 左边文字部分 -->
      <div class="media-text">
        <h3 class="media-title">{{ title }}</h3>
        <p class="media-description">{{ description }}</p>
      </div>
      <!-- 右边内容部分 -->
      <div class="media-right">
        <div class="media-image">
          <el-image :src="image" :alt="title" :preview-src-list=[image] />
        </div>
        <div class="media-video">
          <video controls :src="video" />
        </div>
      </div>
    </div>
  </template>
<script>
export default {
  name: 'MediaItem',
  props: {
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    image: {
      type: String,
      required: true
    },
    video: {
      type: String,
      required: true
    }
  }
}
</script>
  <style scoped>
  .media-item {
    display: flex;
    align-items: flex-start;
    gap: 20px;
    padding: 20px;
    border-bottom: 1px solid #ddd;
  }
  /* 左边文字部分 */
  .media-text {
    flex: 6; /* 占70% */
    text-align: left;
  }
  .media-title {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: left;
  }
  .media-description {
    font-size: 1rem;
    color: #555;
  }
  /* 右边内容部分 */
  .media-right {
    flex: 4; /* 占30% */
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .media-image img {
  max-width: 100%; /* 图片最大宽度为父容器宽度 */
  height: auto; /* 默认保持宽高比 */
  object-fit: cover; /* 裁剪内容以适应容器 */
  border-radius: 5px;
}
/* 如果图片高度大于宽度，限制最大高度为300px */
.media-image img {
  max-height: 250px; /* 限制最大高度 */
  width: auto; /* 宽度自适应 */
}
  .media-video video {
    width: 100%;
    border-radius: 5px;
  }
</style>



